
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f0f0f5;
    color: #333;
  }
  
  .payment-container {
    max-width: 1200px;
    margin: 50px auto;
    padding: 20px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    font-size: 1rem;
    color: #555;
    display: block;
    margin-bottom: 5px;
  }
  
  .form-group input,
  .form-group select {
    width: 100%;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    transition: border-color 0.3s ease;
  }
  
  .form-group input:focus,
  .form-group select:focus {
    border-color: #7a4e9f;
    background-color: #f8f8f8;
    outline: none;
  }
  

  .btn {
    width: 100%;
    padding: 10px;
    background-color: #7a4e9f;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: #6a3991;
  }
  
  .plans-section {
    padding: 20px 10px;
  }
  
  .plans-title {
    font-size: 1.8rem;
    color: #555;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .plans-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .plan-card {
    background: #f9f9f9;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 15px;
    width: 30%;
    text-align: center;
    transition: transform 0.2s ease, box-shadow 0.3s ease;
  }
  
  .plan-card:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
  
  .plan-card h4 {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 10px;
  }
  
  .plan-card p {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 10px;
  }
  
  .btn-secondary {
    padding: 8px;
    background-color: #7a4e9f;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn-secondary:hover {
    background-color: #6a3991;
  }