* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f0f0f5;
    color: #333;
  }
  
  .success-container {
    max-width: 600px;
    margin: 100px auto;
    padding: 20px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .success-container h2 {
    font-size: 2rem;
    color: #28a745; /* Success green color */
    margin-bottom: 20px;
  }
  
  .success-container p {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 15px;
  }
  
  .btn {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #7a4e9f;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: #6a3991;
  }