/* General Page Styling */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f9;
    color: #333;
    margin: 0;
    padding: 0;
  }
  
  /* Hero Section */
  .hero-section {
    background: linear-gradient(135deg, #6a0dad, #d28fdd);
    color: rgb(255, 255, 255);
    padding: 40px 0;
    text-align: center;
  }
  
  .hero-section h1 {
    font-size: 2.5em;
  }
  
  .hero-section p {
    font-size: 1.2em;
  }
  
  .cta-button {
    background-color: #d28fdd;
    color: white;
    padding: 15px 30px;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    font-size: 1.2em;
    transition: background 0.3s ease, transform 0.3s ease;
    border: none;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .cta-button:hover {
    background-color: #a55bb5;
    transform: scale(1.05);
  }
  
  .cta-button:disabled {
    background-color: #b8b8b8;
    cursor: not-allowed;
  }
  
  /* Directions Input */
  .directions-input {
    background: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
  }
  
  .directions-input h2 {
    color: #6a0dad;
    flex-basis: 100%;
    margin-bottom: 10px;
  }
  
  .directions-input input,
  .directions-input select {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .directions-input button {
    flex-basis: 100%;
  }
  
  /* Map */
  #map {
    height: 400px;
    margin: 20px 0;
    border-radius: 5px;
  }
  
  /* Direction Steps */
  .direction-steps {
    background: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin: 20px 0;
  }
  
  .step-content {
    background: white;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    font-size: 1em;
    color: #333; /* Black text for directions */
  }
  
  .step-content b {
    color: #000; /* Bold text in directions should also be black */
  }
  
  .step-content div {
    font-size: 0.9em;
    color: #333;
  }
  
  .step-controls {
    display: flex;
    gap: 10px;
  }
  
  .step-controls button {
    flex: 1;
  }
  
  /* Hazard List */
  .hazard-list {
    background: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px 0;
    color: black; /* Ensure all text in the hazard list is black */
  }
  
  .hazard-list h2 {
    color: #6a0dad;
  }

  .hazard-list p {
    color: #000000;
  }
  
  .hazard-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .hazard-list li {
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
    color: black; /* Each hazard entry is styled in black */
  }
  
  .hazard-list li:last-child {
    border-bottom: none;
  }
  
  /* Marker Guide */
  .marker-guide {
    background: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 20px 0;
  }
  
  .marker-guide h3 {
    color: #6a0dad;
  }
  
  .marker-guide p {
    font-size: 1em;
    color: #333;
  }
  
  /* Report Section */
  .report-section {
    background-color: #f9f9f9;
    padding: 30px;
    border-radius: 10px;
    margin: 40px 0;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .report-section h3 {
    font-size: 1.8em;
    color: #6a0dad;
    margin-bottom: 15px;
    font-weight: bold;
  }
  
  .report-section p {
    font-size: 1.2em;
    color: #050505;
    margin-bottom: 25px;
  }
  
  /* Mobile Adjustments */
  @media (max-width: 768px) {
    .hero-section h1 {
      font-size: 2em;
    }
  
    .cta-button {
      font-size: 1em;
      padding: 12px 24px;
    }
  
    #map {
      height: 300px;
    }
  
    .directions-input {
      gap: 5px;
    }
  
    .directions-input input,
    .directions-input select {
      flex-basis: 100%;
    }
  
    .direction-steps {
      padding: 15px;
    }
  
    .hazard-list {
      padding: 15px;
    }
  }
  
/* Style the InfoWindow content */
.gm-style-iw div {
    color: black !important; /* Ensure all text is black */
    font-size: 1em;
    line-height: 1.5;
  }
  
  .gm-style-iw h4 {
    margin: 0 0 10px 0;
    color: black; /* Header text in black */
    font-size: 1.2em;
  }
  
  .gm-style-iw p {
    margin: 5px 0;
    color: black; /* Paragraph text in black */
  }
  