body {
  background-color: #f4f4f9;
  font-family: 'Arial', sans-serif;
}

.profile-container {
  width: 65%;
  max-width: 1100px;
  margin: 80px auto;
  background: white;
  padding: 60px;
  border-radius: 10px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 20px;
}

.profile-card {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 20px;
}

.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #2c3e50;
}

.profile-info {
  font-size: 1.2rem;
  line-height: 1.8;
}

.profile-info p {
  margin-bottom: 10px;
  color: black;
  font-weight: bold;
}

.emergency-contact {
  background-color: #4b4b4b;
  padding: 15px;
  border-radius: 8px;
  color: white;
  margin-top: 10px;
}

.button-container {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.btn {
  padding: 12px 20px;
  background-color: #6a0dad;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.btn:hover {
  background-color: #9d62c6;
}

.loading-text {
  text-align: center;
  font-size: 1.5rem;
  color: gray;
}

.error-text {
  text-align: center;
  font-size: 1.5rem;
  color: red;
}