:root {
    --font-family-main: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    --primary-color: #4A148C;
    --secondary-color: #555555;
    --background-color: #f9f9f9;
    --box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  }
  
  .content-container {
    width: 75%;
    margin: 50px auto;
    padding: 40px;
    background-color: var(--background-color);
    border-radius: 12px;
    box-shadow: var(--box-shadow);
    font-family: var(--font-family-main);
    line-height: 1.6;
    color: #333333;
  }
  
  h1 {
    color: var(--primary-color);
    font-size: 2.8rem;
    margin-bottom: 25px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1.5px;
  }
  
  h2 {
    color: var(--primary-color);
    font-size: 1.8rem;
    margin-top: 20px;
  }
  
  .content-container p {
    font-size: 1rem;
    margin-bottom: 20px;
    color: var(--secondary-color);
  }
  
  ul {
    margin-left: 20px;
    color: var(--secondary-color);
    font-size: 1rem;
  }
  
  ul li {
    margin-bottom: 10px;
  }
  
  @media screen and (max-width: 768px) {
    .content-container {
      width: 90%;
      padding: 20px;
    }
  
    h1 {
      font-size: 2.4rem;
    }
  
    h2 {
      font-size: 1.5rem;
    }
  
    p, ul li {
      font-size: 0.95rem;
    }
  }
  
