  .contact-form .form-group {
    margin-bottom: 20px;
  }
  
  .contact-form label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .contact-form .form-control {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
  }
  
  .contact-form textarea.form-control {
    resize: none;
  }
  
  .form-submit {
    display: inline-block;
    padding: 10px 20px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
  }
  
  .form-submit:hover {
    background-color: #6A1BCC;
  }
  
  .social-media-links {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-top: 50px;
    flex-wrap: wrap;
  }
  
  .social-media-links .social-icon {
    text-decoration: none;
    font-size: 1rem;
    padding: 25px;
    border-radius: 20px;
    color: white;
    font-weight: bold;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s;
  }
  
  .facebook {
    background-color: #3b5998;
  }
  
  .facebook:hover {
    background-color: #365899;
  }
  
  .twitter {
    background-color: #1DA1F2;
  }
  
  .twitter:hover {
    background-color: #0d95e8;
  }
  
  .instagram {
    background-color: #E4405F;
  }
  
  .instagram:hover {
    background-color: #c13584;
  }
  
  .linkedin {
    background-color: #0077b5;
  }
  
  .linkedin:hover {
    background-color: #005c8e;
  }
  
  .youtube {
    background-color: #FF0000;
  }
  
  .youtube:hover {
    background-color: #e60000;
  }
  